





















import Vue from 'vue';
import {Component} from 'vue-property-decorator';
import Button from '@/components/Button.vue';
import store from '@/store/index2';


@Component({
  components: { Button}
})
export default class Tip extends Vue {
  tags = store.tagList;

  createTag() {
    const name = window.prompt('');
    if (name) {
      store.createTag(name);
    }
    else if(name===null){
      return
    }else{
      window.alert("")
    }
  }
}
